/*About me*/
.aboutpage {
  position: relative;
  height: 100vh;
  overflow: hidden;
  padding: 0 15px;
  display: flex;
  align-content: center;
  color: #fff;
  z-index: 0;
}
.aboutpage > div {
  width: 100%;
  align-self: center;
}

.aboutimg {
  position: absolute;
  right: 0;
  height: 100vh;
  background: rgba(250, 0, 250, 0.8);
  top: -15%;
  left: 60%;
  border-radius: 118px;
  transform: rotate(-30deg);
  overflow: hidden;
  z-index: -1;
}
.aboutimg::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url("../../images/ajitsingh.jpg");
  background-position: center;
  transform: rotate(30deg) scale(1.2);
  top: -19%;
  left: -35%;
  animation: changeimg 5s 0ms infinite;
  background-size: cover;
}
.aboutimg::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url(https://ui-ex.com/images/dust-transparent-smoke-4.png);
  background-repeat: repeat;
  transform: rotate(30deg) scale(1.5);
  top: -7%;
  left: -25%;
  animation: midground 5s 0ms infinite;
  filter: blur(5);
  background-size: cover;
}

@keyframes changeimg {
  0% {
    filter: opacity(1) contrast(1.1);
  }
  50% {
    filter: opacity(0.9) contrast(1);
  }
  100% {
    filter: opacity(1) contrast(1.1);
  }
}
@keyframes midground {
  0% {
    transform: scale(1.7) translateY(-10%) rotate(15deg);
    opacity: 0;
  }
  50% {
    transform: scale(1.9) translateY(0);
    opacity: 0.5;
  }
  100% {
    transform: scale(1.7) translateY(10%) rotate(-15deg);
    opacity: 0;
  }
}
